@import "../../_globalColor.scss";

.project-heading {
    font-size: 56px;
    font-weight: 400;
}
.project-text-div {
    margin-left: 50px;
}

.project-main-div {
    display: flex;
}
.project-main-div > * {
    flex: 1;
    margin-bottom: 20px;
}

.project-car-div{
    position:relative;
    text-align:center;
}

/* Media Query */
@media (max-width: 1380px) {
    .project-heading {
      font-size: 40px;
    }
}
@media (max-width: 768px) {
.project-heading {
    font-size: 30px;
    text-align: center;
}
.greeting-text-p {
    font-size: 16px;
    line-height: normal;
}
.project-main-div {
    flex-direction: column
}
.project-text-div {
    margin-left: 0px;
    margin: 20px;
}
.project-text {
    font-size: 16px;
}
.project-text-subtitle {
    font-size: 16px;
    text-align: center;
}
.project-card-div{
    width: auto;
    height: auto;
}
}